export function loadShader(shaderName: string): Array<string> {
    try {
        var vertexShader = require('./' + shaderName + '/V.glsl')
        var fragShader = require('./' + shaderName + '/F.glsl')
        return [vertexShader, fragShader]
    }
    catch(err) {
        console.error('failed to fetch shader', err)
        return ['', '']
    }
}